*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  font-size: 62.5%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

/* IE10+ doesn't honor `<meta name="viewport">` in some cases. */
@at-root {
  @-ms-viewport {
    width: device-width;
  }
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
	margin: 0;
	font-family: $sans-serif-family;
	font-size: $font-size-base;
	font-weight: $font-weight-normal;
	line-height: $line-height;
	color: $font-color-base;
	background: $white-color;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	text-align: left;
	height: 100%;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

abbr[title],
abbr[data-original-title] { 
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

div {
  position: relative;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: $font-weight-normal;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: $font-weight-bold; 
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub { bottom: -.25em; }
sup { top: -.5em; }


a {
  color: $iaa-blue;
  text-decoration: none;
  background-color: transparent; 
  -webkit-text-decoration-skip: objects;

  &:hover {
    text-decoration: underline;
  }
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;

  &:hover{
    color: inherit;
    text-decoration: none;
  }

  &:focus {
    outline: 0;
  }
}

/* 
  Code
*/

pre,
code,
kbd,
samp {
  font-family: monospace, monospace; 
  font-size: 1em;
}

pre {
  -ms-overflow-style: scrollbar;
}

/* 
	Figures
*/

figure {
  margin: 0 0 1rem;
}

/* 
	Images and content
*/

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden; 
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

/* 
	Tables
*/

table {
  border-collapse: collapse;
}

th {
  text-align: inherit;
}

/* 
	Forms
*/

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  outline: none;
  border: none;
  background: none;
  border-radius: 0;
}

button:focus {
  outline: none;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none; 
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}


input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  resize: vertical;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit; 
  white-space: normal;
}

progress {
  vertical-align: baseline; 
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

