.pagination {
	padding: 1rem;
	border: 1px solid #CCCCCC;
	background: #F4F4F4;
	text-align: center;
	overflow: hidden;
	height: 5.2rem;

	.pages {
		border: 1px solid #CCCCCC;
		background: #FFFFFF;
		display: inline-block;
		border-radius: 4px;
		overflow: hidden;
		height: 3rem;

		.icon {
			fill: #1D67B2;
			top: 0.1rem;
		}

		button {
			width: 3rem;
			height: 3rem;
			text-align: center;
			color: #1D67B2;
			border-right: 1px solid #CCCCCC;
			display: block;
			float: left;
			overflow: hidden;
			padding: 0;

			&:last-of-type {
				border: none;
			}

		}

		.active {
			color: #434343;
			background: #F4F4F4;
		}
	}

	.icon {
		width: 1.2rem;
		height: 1.2rem;
	}

	.btn-secondary {
		border-color: #CCCCCC;
		font-size: 1.4rem;
	}

	.pull-left .icon {
		float: left;
		margin-right: 0.5rem;
	}

	.pull-right .icon {
		float: right;
		margin-left: 0.5rem;
	}
}

