//
// Navs
// --------------------------------------------------


// Base class
// --------------------------------------------------

.nav {
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
    @include clearfix;

    > li {
        position: relative;
        display: block;

        > a {
            position: relative;
            display: block;
            padding: 1rem 1.2rem;
            border-radius: 3px;

            &:hover {
                text-decoration: none;
                background-color: rgba(255, 255, 255, 0.2);
            }
        }

        &.disabled > a {
            color: #777;

            &:hover,
            &:focus {
                color: #777;
                text-decoration: none;
                cursor: not-allowed;
                background-color: transparent;
            }
        }
    }

    .open > a {
        &,
        &:hover,
        &:focus {
            background-color: rgba(255, 255, 255, 0.2);
        }
    }
}


